import { useMediaQuery } from "react-responsive";
import { useNavigate } from "react-router-dom";
import { ReactComponent as Logo } from "../icons/logo.svg";
import SearchBar from "./SearchBar";
import CustomButton from "./CustomButton";
import { useStore } from "../store";

const HeaderNoAuth = () => {
  const setUser = useStore(state => state.setUser)

  const navigate = useNavigate();
  const isXs = useMediaQuery({
    query: "(min-width : 480px)",
  });
  return (
    <div className="flex justify-between items-center px-2 md:px-6 border-b border-one">
      <div className="cursor-pointer" onClick={() => navigate("/ourPrograms")}>
        <Logo width={65} height={65} />
      </div>
      <div className="grow max-w-[500px]  md:mx-10 mx-2 ">
        <SearchBar />
      </div>
      <div className="whitespace-nowrap">
        {isXs && <CustomButton onClick={() => setTimeout(() => setUser("hous"), 2000)} reversed title="sign in" />}
        <CustomButton onClick={() => navigate("/signup")} title="sign up" />
      </div>
    </div>
  );
};

export default HeaderNoAuth;
