import React from "react";

const CustomButton = ({ title, reversed, onClick }) => {
  const reversedStyles =
    "bg-white text-one before:bg-one after:bg-one hover:text-white";
  const normalStyles =
    "bg-one text-white before:bg-white hover:text-one  after:bg-white";
  return (
    <button
      onClick={onClick}
      className={`${
        reversed ? reversedStyles : normalStyles
      } text-sm sm:text-lg rounded-xl overflow-hidden text-semibold m-1 md:px-6 px-2 py-1 border-2 border-one transition-all before:h-0 before:absolute duration-500 before:w-full relative before:left-0 before:top-0 before:z-[-1] z-[1] after:h-0 after:absolute after:w-full after:left-0 after:bottom-0 after:z-[-1] before:transition-all before:duration-500 after:transition-all after:duration-500 hover:before:h-1/2 hover:after:h-1/2`}
    >
      <p>{title}</p>
    </button>
  );
};

export default CustomButton;
