import { ReactComponent as SearchIcon } from "../icons/search.svg";

const SearchBar = () => {
  return (
    <label
      className="flex rounded-tlbr15 2xs:rounded-tlbr20 border border-one overflow-hidden items-center"
      style={{ boxShadow: "2px 2px 8px -2px #061628" }}
    >
      <div className=" rounded-tlbr15 2xs:rounded-tlbr15 bg-one px-2 2xs:px-4  sm:px-6 py-2 sm:py-3 flex items-center justify-center">
        <SearchIcon className="w-[18px] h-[18px]" />
      </div>
      <div className=" flex-1">
        <input type="text" className="w-full outline-none py-1 sm:py-2 px-2" placeholder="Search" />
      </div>
    </label>
  );
};

export default SearchBar;
